<template>
  <v-container fluid fill-height style="padding:0;" id="serviceworker" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="ServiceWorker_demo">serviceWorker使用示例</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>serviceworker</code>是实现pwa的关键核心技术，其中包含一些其他技术<code>cache</code>、<code>fetch</code>、<code>notification</code>、<code>client</code></p>
                    <p class=".body-1"><code>serviceworker</code>中的推送消息功能<code>pushapi</code>在实际应用中存在很多限制（谷歌服务无法访问等），这里不写此技术</p>
                    <p class=".body-1">下面直接上demo，各种技术融入其中，分别介绍</p>

                    <v-tabs color="light-green darken-1" dark slider-color="yellow">
                        <v-tab key="app" ripple>app.js</v-tab>
                        <v-tab key="sw" ripple>sw.js</v-tab>
                        <v-tab-item key="app">
                            <v-card flat>
                                <v-card-text>
                                    <pre class="code">{{pres[0]}}</pre>
                                </v-card-text>
                            </v-card>
                        </v-tab-item>
                        <v-tab-item key="sw">
                            <v-card flat>
                                <v-card-text>
                                    <pre class="code">{{pres[1]}}</pre>
                                </v-card-text>
                            </v-card>
                        </v-tab-item>
                    </v-tabs>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="ServiceWorker_cache">cache</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>cache</code>对象可以单独使用用来将文件缓存到浏览器缓存中，一般缓存上限为计算机内存的一半</p>
                    <p class=".body-1"><code>cache</code>对象一般都在sw中使用，sw中的钩子函数有很好的自定义浏览器请求资源的功能，可以在合适的时机缓存，更新，删除<code>cache</code>中保存的文件</p>
                    <p class=".body-1"><code>cache</code>对象的使用在示例已经演示了，这里单独看看<code>cache</code>中的api</p>
                    <pre class="code">{{pres[2]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="ServiceWorker_client">client</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>client</code>的作用主要是来控制客户端的一些行为，和向客户端发送消息的</p>
                    <pre class="code">{{pres[3]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
