  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
    const handleNotification = (register) => {
        // 检查Notification是否可用，Notification用来实现通知用户消息功能
        if (window.Notification && Notification.permission !== "granted") {
            Notification.requestPermission(function (status) {
                if (Notification.permission !== status) {
                    Notification.permission = status;
                }
            });
        }

        const button = document.querySelector("#btn");

        button.addEventListener('click', function () {
            
            if (window.Notification && Notification.permission === "granted") {
                // 如果用户允许通知
                register.showNotification(
                    "Hi! ", // 标题
                    {
                        body: "我是通知信息", 
                        icon: "./test/xneg.jpg",    // 通知框的图标
                        actions: [{                 // 自定义通知框的更多下拉框的子功能
                            action: 'handle-show',
                            title: '进入网站'
                        }],
                        tag: 'my_notification',     // 唯一标志
                        renotify: true              // 是否可以重复通知
                    }
                );
            } else if (window.Notification && Notification.permission !== "denied") {
                // 如果用户没有表示是否允许通知，这里再发送一次提示是否允许通知
                Notification.requestPermission(function (status) {
                    if (status === "granted") {
                        // 如果用户允许通知
                        register.showNotification(
                            "Hi! ", // 标题
                            {
                                body: "我是通知信息", 
                                icon: "./test/xneg.jpg",
                                actions: [{
                                    action: 'handle-show',
                                    title: '首页'
                                }],
                                tag: 'my_notification',
                                renotify: true
                            }
                        );
                    } else {
                        // 如果拒绝，用alert弹框提示
                        alert("Hi!");
                    }
                });
            }
            else {
                // 如果拒绝，用alert弹框提示
                alert("Hi!");
            }
        });
    };

    const handleServiceWorker = () => {
        if('serviceWorker' in navigator) {
            navigator.serviceWorker.register(
                '/test/serviceworker/sw.js',            // 指定serviceWorker的js文件，相对于网站的根目录
                {
                    scope: '/test/serviceworker/'       // 指定你需要sw控制的目录
                }
            ).then(register => {
                if(register.installing) {
                    console.log('正在安装sw');
                } else if(register.waiting) {
                    console.log('sw安装成功');
                } else if(register.active) {
                    register.active.postMessage({
                        msg: "从client中向serviceWorker发送消息"
                    });
                    console.log('sw已经激活');
                }

                handleNotification(register);
            }).catch(err => {
                console.log('注册失败', err);
            })
        
            navigator.serviceWorker.addEventListener('message', e => {
                // 接受serviceworker发送过来的消息
                console.log(e.data.msg, e.data.url);
            });
        }
    };


    window.onload = function() {
        handleServiceWorker();
    }
    `,
    `
    // 更新缓存版本，新版本的cache会自动安装并且进入waiting状态
    // 当关闭页面重新打开页面，才会激活新版本的cache和触发activate函数
    // 如果需要修改版本马上看到效果，就需要在install事件中使用skipWaiting跳过等待，并且在activate中使用claim通知客户端更新
    const cacheVersion = 'v1'; 

    const cacheList = [ // 指定要缓存资源的路径，相对于根目录
        './test/tiles.jpg',
        './test/xneg.jpg',
    ];

    const noCacheList = [ // 指定不需要缓存资源的路径
        "http://localhost:8002/test/serviceworker/app.js"
    ]

    const updateCache = (request) => {
        return fetch(request).then(response => {
            return caches.open(cacheVersion).then(cache => {
                if(response.ok && noCacheList.indexOf(response.url) === -1){
                    // 网络中包含所需文件，并且成功获取，则将文件缓存到cache中
                    cache.put(request, response.clone()); // 必须要调用clone方法
                }
                return response;
            })
        }).catch(err => {
            return Promise.reject('网络出错无法更新文件：' + request.url);
        })
    }

    // 缓存资源文件
    self.addEventListener('install', e => {
        e.waitUntil( // 只用waitUntil中的代码成功的执行，sw才会安装成功，主要用来延长serviceworker各个钩子函数的生命周期
            caches.open(cacheVersion).then(cache => {
                return cache.addAll(cacheList);
            })
            // .then(() => {
            //     return self.skipWaiting();
            // })
        );
    })

    self.addEventListener('fetch', e => {
        if (e.clientId) {
            clients.get(e.clientId).then(client => {
                client.postMessage({
                    msg: "从serviceWorker中向client发送消息",
                    url: e.request.url
                });
            })
        } else {
            // 无法访问客户端
        }

        e.respondWith( // 用来拦截浏览器的默认fetch请求资源
            caches.match(e.request).then(response => {
                if(response !== undefined) { 
                    // 如果缓存中存在，则从缓存中获取
                    console.log("缓存中存在：" + e.request.url);
                    return response;
                }else{
                    // 如果缓存中不存在，则从网络中获取，并且添加到缓存中
                    console.log("缓存中没有：" + e.request.url);
                    return updateCache(e.request).catch(function (err) {
                        console.log(err);
                    });
                }
            })
        );
    })

    self.addEventListener('activate', e => {
        e.waitUntil(
            caches.keys().then((keyList) => {
                return Promise.all(keyList.map((key) => {
                    // 删除旧版本cache缓存的内容
                    if (key !== cacheVersion) {
                        return caches.delete(key);
                    }
                }))
                // .then(() => {
                //     return self.clients.claim();
                // })
            })
        );
    });

    self.addEventListener('message', e => {
        console.log(e.data.msg);
    });

    self.addEventListener('notificationclick', e => {
        // 在showNotification中注册的actions属性可以在通知提示框的更多中展示出来，当用户点击对应的按钮，会触发此函数
        e.waitUntil(
            clients.matchAll().then((clientsList) => {
                let flag = false;
                clientsList.forEach(client => {
                    if(!flag && client.url.indexOf('index.html') !== -1) {
                        // 如果有一个client正是首页，那么我们就直接显示这个页面
                        flag = true;
                        client.focus();
                    }
                })

                if(!flag) {
                    // 如果所有的client都不是首页，那么直接打开一个首页的client
                    clients.openWindow("http://localhost:8002/test/serviceworker/index.html");
                }
            })
        )
    });
    `,
    `
    caches是cache的管理者
        1. 获取cache对象
            caches.open(cacheVersion).then(cache => {})
        
        2. 判断资源文件是否在缓存中
            request一般指浏览器发送的request对象实例
            caches.match(request).then(response => {
                判断缓存中是否存在请求的资源，response是从缓存中取的资源
                if (response !== undefined) {
                    // 资源存在
                } else {
                    // 资源不存在
                }
            }))

        3. 获取所有的cache版本
            caches.keys().then((keyList) => { })

        4. 判断是否有指定的版本
            caches.has(cacheVersion).then(function(boolean) { })
        
        5. 删除cache版本
            caches.delete(cacheVersion).then(() => {}) 

    cache是缓存资源的缓存对象
        1. 将耽搁文件添加到cache缓存中，返回promise
            add方法会自动的去fetch资源然后调用put方法将资源存到cache中
            cache.add(request)
        
        2. 将文件数组添加到cache缓存中，返回promise
            cache.addAll(url[])
       
        3. 删除某个缓存的文件，返回一个promise
            cache.delete(request)

        4. 获取已经缓存资源的request对象数组
            cache.keys().then(function(keys) {
                console.log(keys);
            })
        
        5. 判断缓存中是否存在请求的资源，response是从缓存中取的资源
            cache.match(request).then(response => {
                if (response !== undefined) {
                    // 资源存在
                } else {
                    // 资源不存在
                }
            }))

        6. 匹配所有，返回数组
            cache.matchAll(request).then(function(response) { });

        7. 将一条资源添加到cache中，一般要配合fetch使用，或者你直接使用add方法
            cache.put(request, response);
    `,
    `
    clients是client的管理者
        1. 让所有的client打开新的页面，返回一个promise
            clients.openWindow(url) 
        
        2. 返回所有的client列表
            clients.matchAll().then((clientsList) => {})
        
        3. 获取指定id的client
            clients.get(clientId).then(client => {})
        
        4. 激活所有clients当前的serviceworker
            clients.claim()

    client
        client.postMessage({
            msg: "我正在从你那里获取资源！"
        });
    `
],
      time: "2019-07-12",
      title: "service-worker",
      items: [
        {
          title: "serviceWorker使用示例",
          target: "#ServiceWorker_demo"
        },
        { divider: true },
        {
          title: "cache",
          target: "#ServiceWorker_cache"
        },
        { divider: true },
        {
          title: "client",
          target: "#ServiceWorker_client"
        }
      ],
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

